.root {
    background: var(--Color---1, #F5F7FF);
    display: flex;
    justify-content: space-between;
    text-align: center;
    justify-content: center;
    gap: 0 130px;
    margin-top: 61px;
    padding: 60px 0 45px 0;
    @media (max-width:1100px) {
        flex-wrap: wrap;
        justify-content: space-around;
        gap: 50px 130px;
    }
    img {
        margin-bottom: 24px;
        max-width: 65px;
    }
    h3 {
        color: #000;
        text-align: center;
        font-family: Poppins;
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 132.5%; 
        max-width: 265px;
    }
    p {
        color: #000;
        text-align: center;
        font-family: Poppins;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
        max-width: 265px;
    }
}